// Импортируем глобальные переменные
@import "master";

* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  padding: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  // font-family: "Raleway", sans-serif;
  font-family: "Open Sans", sans-serif;
  color: #313131;

  // remove on work
  // just to make sure css imported
}
.main-content {
  flex: 1 0 auto;
}
.main-footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

.main-logo {
  width: 130px;
  height: 52px;
}
.footer-logo {
  width: 150px;
}

.semi-bold {
  font-weight: 600;
}

.dd-button {
  display: inline-block;
  padding: 0.625rem 3.75rem;
  text-decoration: none !important;
  transition: 0.3s;
  border: solid 2px #313131;
  color: #313131;
  font-weight: 600;

  &:hover {
    color: #fff;
    background-color: #313131;
    &.dd-button--gradient {
      color: #fff;
      box-shadow: none;
    }

    &.dd-button--filled {
      &.dd-button--gradient {
        color: transparent;
        box-shadow: 2px 1000px 1px #fff inset;
      }
    }
  }

  &__text {
    font-weight: 600;
    font-size: 1.125rem;
  }

  &--gradient {
    color: transparent;
    border-color: transparent;
    background-image: linear-gradient(
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 0)
      ),
      linear-gradient(90deg, #ff604a, #ff2b6a);
    background-origin: border-box;
    background-clip: content-box, border-box;
    box-shadow: 2px 1000px 1px #fff inset;

    .dd-button__text {
      background: linear-gradient(90deg, #ff604a, #ff2b6a);
      background-clip: text;
    }
  }
  &--filled {
    color: #fff;
    box-shadow: none;
  }
}

.dd-input {
  padding: 9px 15px;
  padding-left: 20px;
  font-size: 20px;
  border: 2px solid #f2f2f2;
  outline: none;
  &::placeholder {
    color: #828282;
  }

  &--inline {
    margin-right: -5px;
  }
}

.carousel-image {
  object-fit: cover;
  min-height: 320px;
}

section {
  position: relative;
  padding: 3.125rem 0;
}

// background-image: url(http://localhost:4100/assets/home_section_3.png);
// background-repeat: no-repeat;
// background-position: top right;
// background-size: contain;

.out-of-bound-img {
  position: absolute; // section must be relative
  bottom: 0;
  right: 0;
  // max-width: 625px;
  width: 54%;
  max-width: 800px;
  &--full {
    width: 50%;
    height: 100%;
    object-fit: cover;
    object-position: left;
  }
}

.main-header {
  background-color: #232323;
  a {
    color: #fff;
    &:hover {
      text-decoration: none;
    }
  }
  &__nav {
    line-height: 3.75rem;
    font-size: 0.875rem;
    font-weight: 600;
    flex-wrap: wrap;
    li {
      margin-right: 1.25rem;
      position: relative;
      &.active {
        a {
          // add gradient
          // color: #ff604a;
          background: linear-gradient(90deg, #ff604a, #ff2b6a);
          background-clip: text;
          -webkit-text-fill-color: transparent;
          text-shadow: 0 0 2px rgba(255, 97, 77, 0.37);
        }
        &::before {
          position: absolute;
          content: "";
          top: 0;
          left: 0;
          width: 100%;
          height: 2px;
          background: linear-gradient(90deg, #ff604a, #ff2b6a);
        }
      }
    }
  }
}

.call-me {
  &-wrapper {
    background-color: #ff604a;
    background: linear-gradient(90deg, #ff604a, #ff2b6a);
    line-height: 3.9rem;
    transition: 0.4s;
    cursor: pointer;
    &:hover {
      opacity: 0.8;
    }
  }
  display: block;
  padding: 0 1.25rem;
  white-space: nowrap;
  font-size: 1.125rem;
  font-weight: 600;
}

.section-title {
  font-size: 3.125rem;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
}

.section-subtitle {
  font-size: 1.5rem;
  font-weight: 600;
}

.small-title {
  font-size: 1.125rem;
  font-weight: 600;
}

.carousel-content {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
}
.carousel-title {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 3.125rem;
}
.carousel-subtitle {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 4.375rem;
}
.carousel-text {
  font-size: 1.125rem;
}

.accent-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #828282;
  text-transform: uppercase;
}

.product {
  background: #ffffff;
  text-align: center;
  transition: 0.5s;

  &:hover {
    box-shadow: 0px 8px 30px rgba(176, 176, 176, 0.2);
    .product__overlay {
      opacity: 1;
      user-select: all;
    }
  }

  &__header {
    position: relative;
  }
  &__image {
    width: 100%;
  }

  &__new-label {
    width: 34px;
    position: absolute;
    top: 13px;
    right: 15px;
  }
  &__overlay {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    user-select: none;
    transition: 0.3s;
    &-content {
      margin-top: auto;
      text-align: center;
      width: 100%;
      margin-bottom: 1rem;
    }
    &-button {
      text-decoration: none !important;
      &:not(:last-child) {
        .product__overlay-icon-wrapper {
          margin-right: 1.25rem;
        }
      }
      &:hover {
        .product__overlay-icon-wrapper {
          background-color: rgba(#313131, 1);
        }
      }
    }
    &-icon {
      &-wrapper {
        display: inline-block;
        background-color: rgba(#313131, 0.5);
        border-radius: 50%;
        padding: 10px;
        padding-top: 8px;
        padding-bottom: 8px;
        transition: 0.5s;
        color: #fff;
      }
    }
  }

  &__name {
    font-size: 1.125rem;
    margin-top: 1.25rem;
    margin-bottom: 0.625rem;
  }
  &__price {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.9375rem;
  }
}

.intersection {
  max-width: 792px;
  margin: auto;
  margin-top: -175px;
  text-align: center;
  background: #fff;
  box-shadow: 0px 6px 40px rgba(196, 194, 194, 0.2);
  padding: 35px 53px;

  &__title {
    font-size: 1.5rem;
    font-weight: 600;
  }
}

.advantage {
  color: #313131;
  text-align: center;
  padding: 1.375rem 1.5rem;
  transition: 0.15s;
  cursor: pointer;

  &__icon {
    font-size: 3.125rem;
    margin-bottom: 1.875rem;
    display: block;
  }

  &__title {
    font-size: 1.125rem;
    font-weight: 600;
  }

  &__text {
    font-size: 15px;
  }

  &:hover {
    color: #fff;
    background-color: #ff604a;
    background: linear-gradient(90deg, #ff604a, #ff2b6a);
    box-shadow: 0px 8px 30px rgba(255, 65, 110, 0.2);
  }
}

.main-footer {
  background-color: #232323;
  color: #fff;
  padding: 30px 0;
  padding-bottom: 0.625rem;
  a {
    color: #fff;
  }
  &__title {
    font-size: 0.875rem;
    font-weight: bold;
  }
  &__nav {
    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        margin-bottom: 0.5rem;
      }
    }
  }
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999999;
  display: none;
  &.active {
    display: flex;
  }

  &__content {
    position: relative;
    background-color: #fff;
    width: 100%;
    max-width: 375px;
    &-close-btn {
      position: absolute;
      top: 10px;
      right: 10px;
      border: none;
      background: none;
      color: #fff;
      font-size: 16px;
      font-weight: bold;
      outline: none !important;
    }
    &-header {
      background: linear-gradient(90deg, #ff604a, #ff2b6a);
      padding: 26px 34px;
      color: #fff;
      text-align: center;
      font-weight: 600;
      font-size: 1.5rem;
    }
    &-body {
      padding: 30px;
      color: #828282;
      &-img {
        width: 100px;
      }
    }
    &-form {
      &-input {
        display: block;
        width: 100%;
        font-weight: 600;
        border: none;
        border-bottom: 2px solid #bdbdbd;
        padding-bottom: 10px;
        outline: none !important;
        transition: 0.5s;
        color: #17161b;
        &:focus {
          border-color: #ff604a;
        }
      }
      &-btn {
        width: 100%;
        border: none;
        padding: 12px 15px;
        border-radius: 24px;
        color: #fff;
        background: linear-gradient(90deg, #ff604a, #ff2b6a);
        transition: 0.4s;
        outline: none !important;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
}

.custom-control-input:checked ~ .custom-control-label::after {
  filter: invert(1);
}
.custom-control-input:checked ~ .custom-control-label::before {
  border-color: #cecece !important;
  background-color: #fff !important;
}
.custom-checkbox {
  padding-left: 2rem;
  label {
    &::before {
      top: 0.15rem !important;
      left: -2.1rem !important;
      width: 1.25rem !important;
      height: 1.25rem !important;
    }
    &::after {
      top: 0.25rem;
      left: -2rem;
      transform: scale(1.25) !important;
    }
  }
}
.custom-control-input:focus:not(:checked) ~ .custom-control-label::before {
  border-color: #ff604a;
}
.custom-control-input:focus ~ .custom-control-label::before {
  box-shadow: 0 0 0 0.2rem rgba(#ff604a, 0.25);
}
.custom-control-label {
  font-size: 0.75rem;
}
